import Vue from 'vue';
import Vuex from 'vuex';
import firebase from 'firebase';
import router from '../router'
import axios from 'axios';

Vue.use(Vuex)
const createStore = () => {
  return new Vuex.Store({
    modules: {},
    // plugins: [new VuexPersistence().plugin],
    // plugins: [vuexLocal.plugin],
    state: {
      // ABSEN
      lat : -3.44,
      lng : 112.222,
      location : 'OFFICE',
      employee_id:'1',
      hit_id:false,
      origin_id:false,
      user_image:'1',
      employee:{},
      employees:[],
      checktimes:[],
      work_location:'1',
      work_location_id:1,
      timetable:[],
      listAttendance:[],
      type:'in',
      address:"",
      clock:{},
      tensor:[],
      location_lat:-3.4453019,
      location_lng : 114.7660139,

      check_in:'---------',
      check_out:'---------',
      work_from:'---------',
      work_to:'---------',
      worked_hours:'---------',
      work_late:'00:00',
      work_early:'00:00',

      attendances:[],
      absensi:null,
      absenStatus:null,
      qrscanInterval:null,

      // CONFIGURASI SISTEM
      baseURL:'https://hit-attendance.online',
      // baseURL:'http://192.168.150.192:8069',
      faceURL:'https://hitfaceapi.my.id',
      camera:true,
      mobile:true,
      scanSize:416, //128, 160, for detecting smaller faces use larger sizes, e.g. 512, 608 | default: 416
      akurasiWajah:70,
      group_login:false,
      admin_menu:false,
      loading:'false',
      home:'dashboard',
      // ABSEN
      //! ITEMS:
    },
    mutations: {
     
    },
    actions: {
      async sync_absen(vdata){
            window.checktimes.remove({},{multi:true},(err,docs)=>{});
            async function loop(datanya,j=0){
            for(const e of datanya){
                await new Promise(async(resolve,reject)=>{
                    // PROGRESS SEBELUM RESOLVE
                    if(e.source_data=='local'){
                        var fd = new FormData();
                        var blobBin = atob(e.dataURL.split(',')[1]);
                        var array = [];
                        for(var i = 0; i < blobBin.length; i++) {
                            array.push(blobBin.charCodeAt(i));
                        }
                        var file = new Blob([new Uint8Array(array)], { type: 'image/png' });
                        fd.append("id_user", e.id_user);
                        fd.append("force", "true");
                        fd.append('checktime',e.checktime)
                        fd.append("file", file);
                        fd.append("work_location", e.work_location);
                        fd.append("lat", e.lat);
                        fd.append("lng", e.lng);
                        fd.append("type_location", e.location);
                        try{
                            let res = await axios.post(vdata.state.baseURL+"/api/hit/absen?employee_id="+e.id_user, fd)
                            if(res.data.status==true){
                                vdata.state.checktimes[j]['source_data']='server'
                            }
                        }catch(err){
                        }
                    }
                    setTimeout(() => {
                        j++;
                        resolve();
                    }, 0);
                })
            }
        }
        console.log("START")
        await loop(vdata.state.checktimes.slice(0, 5));
        window.checktimes.insert(vdata.state.checktimes,(err,docs)=>{});
        vdata.dispatch('sync_wajah')
      },
      async sync_wajah(vdata){
        window.employees.find({source_data:'local'},(err,docs)=>{
          if(docs){
            docs.forEach(async (e)=>{
              let fd = new FormData();
              fd.append("employee_id", e.hit_id);
              fd.append("faces", e.faces);
              let res = await axios.post(vdata.state.baseURL + "/api/hit/register-face",fd);
            })
            window.employees.update({source_data:'local'},{$set:{source_data:'server'}}, { multi: true },(err,docs)=>{})
          }
        })
      },
      async update_employees(vdata){
        window.employees.remove({},{multi:true},(err,docs)=>{});
        let res2;
        if(vdata.state.group_login){
          res2 = await axios.get(vdata.state.baseURL + "/api/employee/group?hit_id=" + vdata.state.origin_id);
        }else{
          res2 = await axios.get(vdata.state.baseURL + "/api/employee/data?hit_id=" + vdata.state.origin_id);
        }
        vdata.state.employees = res2.data;
        vdata.state.employee = res2.data.filter(e=>e.hit_id==vdata.state.hit_id)[0]
        window.employees.insert(res2.data,(err,docs)=>{})
      }
    },
  })
}

export default createStore